import { BottomNavigationAction, Typography, styled, useTheme } from "@rp/react/web/mui/core"

type BottomNavigationActionProps = Parameters<typeof BottomNavigationAction>[0]

export const BottomNavigationButtonStyled = styled(BottomNavigationAction)(() => ({
  minWidth: 65,
  whiteSpace: "nowrap",
  "& p": {
    fontSize: 12,
  },
}))

export type NavBtnProps = {
  tag: string
  selected: boolean
  icon: React.ReactNode
  value?: string
  iconDisabled?: React.ReactNode
}

export const BottomNavigationButton = ({
  tag,
  selected,
  icon,
  value,
  onChange,
  iconDisabled,
  onClick,
  id,
}: NavBtnProps & BottomNavigationActionProps) => {
  const theme = useTheme()

  return (
    <BottomNavigationButtonStyled
      id={id}
      showLabel
      onChange={onChange}
      value={value}
      label={<Typography>{tag}</Typography>}
      sx={{
        color: selected ? theme.palette.text.primary : theme.palette.grey[300],
      }}
      icon={selected && iconDisabled ? icon : iconDisabled}
      onClick={onClick}
    />
  )
}
